
.btn {
	border: 2px solid grey;
	padding: 10px 14px;
	margin: 200px 10px;
	border-radius: 3px;

	position: relative;
	overflow: hidden;
}

.btn:focus {
	outline: none;
	border: 2px solid brown;
}

.btn > .dot {
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background: pink;
	opacity: 0.4;

	position: absolute;

	-webkit-animation: 0.5s big forwards;

	        animation: 0.5s big forwards;
}


@-webkit-keyframes big{
	0%{-webkit-transform: scale(0.1);transform: scale(0.1)}
	100%{-webkit-transform: scale(15);transform: scale(15)}
}


@keyframes big{
	0%{-webkit-transform: scale(0.1);transform: scale(0.1)}
	100%{-webkit-transform: scale(15);transform: scale(15)}
}



